* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 18px;
  --Almost-White: hsl(0, 0%, 98%);
  --Medium-Gray: hsl(0, 0%, 41%);
  --Almost-Black: hsl(0, 0%, 8%);
  font-family: "Epilogue", sans-serif;
}
body {
  background-color: var(--Almost-White);
  font-weight: 500;
}
@import url("https://fonts.googleapis.com/css2?family=Epilogue:wght@400;700&display=swap");
h1 {
  font-size: 3.5rem;
  font-weight: 700;
}
h2 {
  font-size: 2.45rem;
}
h3 {
  font-size: 2rem;
}
h4 {
  font-size: 1.5rem;
}
p {
  font-size: 1rem;
}
li {
  list-style: none;
}

ul {
  display: flex;
  gap: 1rem;
}
button {
  background-color: transparent;
  border: none;
  text-transform: capitalize;
  padding: 0.75rem 1rem;
  text-align: center;
  color: var(--Medium-Gray);
}
button:hover {
  color: var(--Almost-Black);
  cursor: pointer;
}
button > img {
  margin-left: 0.5rem;
}
.logo {
  margin-right: 2.5rem;
}
@media screen and (min-width: 400px) {
  #backgroundImg {
    content: url("./images/image-hero-desktop.png");
  }
  .forHideSidebar {
    display: none;
  }
}

.main {
  max-width: 1440px;
  margin: 0 auto;
  margin-top: 2rem;
}
.navigation {
  display: flex;
  align-items: center;
  margin: 0.25rem 0;
}
.navbar {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.btn-container {
  margin-left: auto;
  margin-right: 0.5rem;
}
.btn-container > button {
  margin: 0 0.5rem;
}
.btn-container > button:hover {
  color: var(--Almost-Black);
  cursor: pointer;
}

.container {
  margin-left: 7rem;
  margin-top: 1.5rem;
  margin-right: 4rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.wallpaper {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 3;
}
.wallpaper > img {
  width: 450px;
  float: right;
}

.heading {
  margin-top: 7rem;
  letter-spacing: 0.1rem;
}
.heading > h1 > span {
  display: block;
  font-size: 3rem;
  letter-spacing: 0.1rem;
}
.para {
  width: 75%;
  margin-top: 2rem;
  letter-spacing: 0rem;
  line-height: 27px;
  color: var(--Medium-Gray);
}
.partner-container {
  display: flex;
  align-items: center;
  grid-column-start: 1;
  grid-column-end: 2;
}
.partner-container > img {
  width: 80px;
  height: auto;
  margin: 0 1rem;
}
.learnMoreBtn {
  margin-top: 2rem;
  border-radius: 0.5rem;
  background-color: black;
  color: white;
  padding: 0.75rem 1rem;
}
.learnMoreBtn:hover {
  border: 1px solid var(--Almost-Black);
  color: black;
  background-color: white;
  font-weight: 500;
  cursor: pointer;
}
.register-btn {
  border: 1px solid var(--Medium-Gray);
  font-weight: 500;
  cursor: pointer;
  border-radius: 0.5rem;
}
.register-btn:hover {
  border: 1px solid var(--Almost-Black);
  color: var(--Almost-Black);
}

.list {
  display: block;
  padding: 0.75rem 0.5rem;
  flex-direction: row;
  background-color: white;
  border-radius: 0.5rem;
  box-shadow: 3px 3px 30px gray;
}

.list > li {
  text-transform: capitalize;
  padding: 0.25rem 0.5rem;
  color: var(--Medium-Gray);
}
.list > li > img {
  padding: 0rem 0.5rem;
}
.block1 {
  position: absolute;
  left: 160px;
  top: 75px;
}
.block2 {
  position: absolute;
  left: 350px;
  top: 75px;
}

@media screen and (max-width: 375px) {
  #backgroundImg {
    content: url("./images/image-hero-mobile.png");
  }
  .main {
    width: 700px;
    position: relative;
  }
  .container {
    width: 700px;
    margin-top: 1rem;
    display: grid;
    /*  grid-template-columns: repeat(1, 1fr); */
    /* grid-auto-rows: minmax(100px, auto) */
    margin: 0 auto;
  }
  .wallpaper {
    grid-column-start: 1;
    grid-row-start: 1;
  }
  .wallpaper > img {
    width: 100%;
  }
  .heading {
    width: 700px;
    font-size: 2.5rem;
    margin-top: 2rem;
    letter-spacing: 0rem;
    text-align: center;
  }

  .heading > h1 > span {
    display: inline;
    letter-spacing: 0rem;
  }
  .para {
    width: 80%;
    margin-inline: auto;
    margin-top: 2rem;
    font-size: 1.5rem;
    letter-spacing: 0rem;
    line-height: 40px;
    font-weight: 500;
  }
  .partner-container {
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .partner-container > img {
    width: 100px;
    height: auto;
    margin: 2.5rem 0.5rem 1rem 0.5rem;
  }
  .navbar {
    display: none;
  }
  .logo > img {
    width: 150px;
  }
  ul {
    display: block;
    gap: 1rem;
  }
  .navigation {
    display: flex;

    justify-content: space-between;
  }
  .sidebar {
    height: auto;
    position: absolute;
    background-color: white;
    height: 100%;
    width: 50%;
    margin-top: -1rem;
    top: 0;
    right: 0;
    box-shadow: 1px 1px 5000px rgb(0, 0, 0);
  }
  .btn-container {
    display: flex;
    flex-direction: column;
  }
  .menuBtn {
    width: 100%;
    text-align: right;
  }
  .sidebar {
    padding-top: 0.5rem;
  }
  .sidebarList > li > button {
    width: 100%;
    text-align: left;
  }
  .subFeatures {
    margin: 1rem 0;
    margin-left: 0.5rem;
  }
  .sidebarList > li > button {
    font-size: 1.25rem;
  }
  .btn-container > button {
    font-size: 1.25rem;
  }
  .subFeatures > li {
    width: 100%;
    padding: 0.5rem;
    display: inline-flex;
    gap: 0.5rem;
    font-size: 1.25rem;
  }
  .companyList {
    margin: 1rem 0;
    margin-left: 0.5rem;
  }
  .companyList > li {
    width: 100%;
    padding: 0.5rem;
    font-size: 1.25rem;
  }
  .forHide {
    display: none;
  }
}
